<template>
  <div class="block_content array">
    <ol class="array-ol">
      <li
        v-for="(member, index) in value"
        :key="`${member.type}${index}`"
        :class="['array-item', { 'hide-item': hideMyItem[index] === true }]"
      >
        <p v-if="!['object', 'array'].includes(member.type)">
          <template
            v-if="
              member.type === 'variable' || isValidateVariable(member.remark)
            "
          >
            <el-tag
              v-if="member.remark.length > 6"
              :title="formatTip(member.remark)"
              class="value-tag"
              closable
              disable-transitions
              @click="handleShowVariableSelect(member, 'remark')"
              @close="member.remark = 'app.$b'"
            >
              {{ formatTag(member.remark) }}
            </el-tag>
            <el-button
              v-else
              @click="handleShowVariableSelect(member, 'remark')"
              >选择变量
            </el-button>
            <el-tag
              v-if="member.remark.length > 6"
              :title="getElementName(member.remark)"
              class='value-tag'
              style='margin-left: 10px;'
              :type='getTagType(member.remark)'
            >{{formatTag(getElementName(member.remark), 12)}}</el-tag>
          </template>
          <el-input
            :title="formatTip(member.remark)"
            type="text"
            v-model="member.remark"
            class="val-input"
            v-if="member.type === 'string' && !isValidateVariable(member.remark)"
            placeholder="string"
          />
          <el-input
            :title="formatTip(member.remark)"
            type="number"
            v-model.number="member.remark"
            class="val-input"
            v-if="member.type === 'number'"
            placeholder="number"
            @input="numberInputChange(member)"
          />
          <el-select
            :title="formatTip(member.remark)"
            name="value"
            v-model="member.remark"
            class="val-input"
            v-if="member.type === 'boolean'"
          >
            <el-option :value="true" label="true"></el-option>
            <el-option :value="false" label="false"></el-option>
          </el-select>
        </p>
        <div v-if="['object', 'array'].includes(member.type)">
          <span :class="['json-key', 'json-desc']">
            {{ member.type.toUpperCase() }}
            <i
              class="collapse-down v-json-edit-icon-arrow_drop_down"
              v-if="member.type === 'object' || member.type === 'array'"
              @click="closeBlock(index, $event)"
            ></i>
            <i v-if="member.type === 'object'">{{
              '{' + member.childParams.length + '}'
            }}</i>
            <i v-if="member.type === 'array'">{{
              '[' + member.childParams.length + ']'
            }}</i>
          </span>

          <span class="json-val">
            <template v-if="member.type === 'array'">
              <array-view v-model="member.childParams"></array-view>
            </template>

            <template v-if="member.type === 'object'">
              <json-view v-model="member.childParams"></json-view>
            </template>
          </span>
        </div>

        <div class="tools">
          <el-select
            v-model="member.type"
            class="tools-types"
            @change="itemTypeChange(member)"
          >
            <el-option
              v-for="item in formats"
              :value="item.value"
              :key="item.value"
              :label="item.label"
            ></el-option>
          </el-select>
          <i class="dragbar v-json-edit-icon-drag"></i>
          <i v-if='editorProvide.showButtons' class="del-btn" title="新增同级节点" @click="addItem">
            <i class="add-btn-primary">+</i>
          </i>
          <i v-if='editorProvide.showButtons' class="del-btn" @click="delItem(value, member, index)">
            <i class="add-btn-primary">-</i>
          </i>
        </div>
      </li>
    </ol>

    <div v-if='!value.length && editorProvide.showButtons' class="block add-key" @click="addItem">
      <i class="add-btn-primary" title="新增子节点">+</i>
    </div>
  </div>
</template>

<script>
  import elementEditor from '@/mixins/elementEditor'

  export default {
    name: 'ArrayView',
    mixins: [elementEditor],
    props: {
      value: {
        type: [Array],
      },
    },
    inject: ['editorProvide', 'callbackId'],
    data: function() {
      return {
        formats: [
          {
            label: '变量',
            value: 'variable',
          },
          {
            label: '文本',
            value: 'string',
          },
          {
            label: '数值',
            value: 'number',
          },
          {
            label: '布尔',
            value: 'boolean',
          },
          {
            label: '对象',
            value: 'object',
          },
          {
            label: '数组',
            value: 'array',
          },
        ],
        toAddItem: false,
        hideMyItem: {},
        variableRowDetail: null,
        needName: false
      }
    },
    components: {
      'json-view': () => import('./JsonView.vue'),
    },
    methods: {
      delItem: function(parentDom, item, index) {
        this.value.splice(index, 1)
        if (this.hideMyItem[index]) this.hideMyItem[index] = false
        this.$emit('input', this.value)
      },

      addItem: function() {
        this.editorProvide.addItem(this)
      },

      closeBlock: function(index, e) {
        this.$set(this.hideMyItem, index, !this.hideMyItem[index])
      },

      newItem: function(obj) {
        let oj = {
          name: obj.key,
          type: obj.type,
        }
        if (obj.type === 'array' || obj.type === 'object') {
          oj.childParams = obj.val
          oj.remark = null
        } else {
          oj.childParams = null
          oj.remark = obj.val
        }

        this.value.push(oj)
        this.$emit('input', this.value)
      },

      onDragEnd: function() {
        this.$emit('input', this.value)
      },

      itemTypeChange: function(item) {
        if (item.type === 'array' || item.type === 'object') {
          item.childParams = []
          item.remark = null
        }
        if (item.type === 'boolean') {
          item.remark = true
        }
        if (item.type === 'string') {
          item.remark = ''
        }
        if (item.type === 'number') {
          item.remark = 0
        }
        if (item.type === 'variable') {
          item.remark = 'app.$b'
        }
      },

      numberInputChange: function(item) {
        if (!item.remark) item.remark = 0
      },
      handleShowVariableSelect(row, key, cb) {
        this.editorProvide.handleShowVariableSelect(row, key, cb)
      },
    },
  }
</script>
<style lang="less" scoped src="./styles/common.less"></style>
